<template>
    <footer class="orderBottom wp100">
        <p class="fl">
             <span>{{shopNum}}</span>  <span>{{totalPrice}}</span> 
        </p>
        <button class="fr payBtn" @click="pay"></button>
    </footer>
</template>
<script>
  
    export default {
        data(){
            return{
                shopNum:0,
                totalPrice:0
            }
        } ,
        created(){
            this.$root.$on('totalArr',(data)=>{
                this.shopNum=0;
                this.totalPrice=0;
                // console.log(data)
                data.forEach(function(e) {
                    // console.log(e)
                    if(e.count>0){
                        this.shopNum++;
                        this.totalPrice += e.count*(e.price*100); //100
                    }

                }, this);
                this.totalPrice = (this.totalPrice/100).toFixed(2);
            });

        },
        methods:{
            pay(){
                alert('')
            }
        }
    }
</script>
<style lang="scss" scoped>
    @import '../../../style/page/order.scss'
</style>


