<template>
    <div class="optprice">
        <label class="add red fr" @click="add(pId)">+</label>
        <span class="num fr">{{count}}</span>
        <label :class="['sub', 'fr', {red:count>0}]" @click="sub(pId)">-</label>
    </div>
</template>
<script>
  
    export default {

        props: ['pId','pCount','pPrice'],
        data(){
            return{
                count: 0
            }
        } ,
        mounted(){
            this.$root.$on('vCount',()=>{
                //  nextTick 
                //   http://cn.vuejs.org/v2/guide/reactivity.html#
                this.$nextTick(()=>{
                    this.count = this.pCount[this.pId].count;
                })

            });
                

        },
        methods:{
            add(pId){
                this.count++;
                this.$root.$emit('pidCount',pId,this.count);
            },
            sub(pId){
                if(this.count>0){
                    this.count--;
                    this.$root.$emit('pidCount',pId,this.count);
                }
            }
        },
        watch:{
            count(){
                this.$root.$emit('changeCount')
            }
        }
    }
</script>
<style lang="scss" scoped>
    @import '../../../style/page/order.scss'
</style>


